import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, ViewController, ActionSheetController } from 'ionic-angular';
import { AngularFireDatabase, FirebaseListObservable, FirebaseObjectObservable } from 'angularfire2/database';

import jsPDF  from 'jspdf';
declare let jsPD;
import * as papa from 'papaparse';

/**
 * Generated class for the FormulierPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-formulier',
  templateUrl: 'formulier.html',
})


export class FormulierPage {
  shizzles: FirebaseListObservable<any>;
  shizzleId;
  FormId;
  FormDate;
  FormName;
  FormTekst;
  FormManier;
  form = {
    formuliernaam: '',
    datum: '',
    manier: '',
    gesprek: ''
  };

  constructor(public navCtrl: NavController, public navParam: NavParams, public modalCtrl: ModalController, public viewCtrl: ViewController, public actionSheetCtrl: ActionSheetController, public af: AngularFireDatabase) {
    this.shizzleId = navParam.get("shizzleId");
    this.shizzles = af.list('/shizzles/' + this.shizzleId + '/form/');
    this.FormId = navParam.get("FormId");
    this.FormName = navParam.get("FormName");
    this.FormDate = navParam.get("FormDate");
    this.FormTekst = navParam.get("FormTekst");
    this.FormManier = navParam.get("FormManier");
    this.form = {
      formuliernaam: this.FormName,
      datum: this.FormDate,
      manier: this.FormManier,
      gesprek: this.FormTekst
    };
  }

  public logForm(){
    this.shizzles.update(this.FormId, {
      formuliernaam: this.form.formuliernaam,
      datum: this.form.datum,
      manier: this.form.manier,
      gesprek: this.form.gesprek
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad FormulierPage');
  }
  public pop(): void{
    this.viewCtrl.dismiss();
  }


  public PDFexport(): void{
  var imgData = 'data:image/png;base64,';
    var doc = new jsPDF()
    doc.addImage(imgData, 'JPEG', 130, 0, 80, 50);
    doc.setFont("sans");
    doc.setFontType("normal");
    doc.setFontSize(25);
    doc.text(20, 37, 'Gespreksformulier');
    doc.setFontSize(14);
    doc.text(142, 60, 'leerlingnummer');
    doc.setFontSize(12);
    doc.text(142, 65, this.form.datum);
    doc.setFontSize(14);
    doc.text(142, 70, this.form.manier);
    doc.setFontType("bold");
    doc.text(20, 65, 'Betreft:');
    doc.setFontType("normal");
    doc.text(38, 65, this.form.formuliernaam);
    var gesprek = doc.splitTextToSize(this.form.gesprek, 170);

    doc.text(20, 90, gesprek);
    doc.save(this.form.formuliernaam+" "+this.form.datum+'.pdf');
  }

  public CSVexport(): void{
    var csvstring = [{
	    "Column 1": "1-1",
	    "Column 2": "1-2",
	    "Column 3": "1-3",
	    "Column 4": "1-4"
	}];
let csv = papa.unparse({
      data: csvstring
    });
var blob = new Blob([csv]);
    var a = window.document.createElement("a");
    a.href = window.URL.createObjectURL(blob);
    a.download = "newdata.csv";
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);

  }

  public back(): void{
    this.viewCtrl.dismiss();
  }

  deleteGespreksForm(shizzleId) {
    this.shizzles.remove(this.FormId);
  }

  public deleteForm(FormId) {
    let actionSheet = this.actionSheetCtrl.create({
      title: 'Weet u zeker dat u dit wilt verwijderen',
      buttons: [
        {
          text: 'Verwijderen',
          role: 'destructive',
          handler: () => {
            this.deleteGespreksForm(this.FormId);
          }
        },{
        text: 'Cancel',
        role: 'cancel',
        handler: () => {
          console.log('Cancel clicked');
        }
       }
      ]
    });
    actionSheet.present();
  }



}



